*,*::before,*::after{margin:0;padding:0;box-sizing:border-box;outline:none}html,body{height:100%;width:100%;background-color:#f3f7fa;font-family:'Open Sans', sans-serif;font-weight:600}.spinner{width:40px;height:40px;position:relative;text-align:center;animation:sk-rotate 2s infinite linear}.spinner-wrap{width:100%;height:100%;display:flex;justify-content:center;align-items:center;padding:20px 0}.dot1,.dot2{width:60%;height:60%;display:inline-block;position:absolute;top:0;background-color:#333;border-radius:100%;animation:sk-bounce 2s infinite ease-in-out}.dot2{top:auto;bottom:0;animation-delay:-1s}@keyframes sk-rotate{100%{transform:rotate(360deg);-webkit-transform:rotate(360deg)}}@keyframes sk-bounce{0%,100%{transform:scale(0);-webkit-transform:scale(0)}50%{transform:scale(1);-webkit-transform:scale(1)}}.app{max-width:960px;margin:0 auto;padding:0 100px}.app-main{width:100%;display:flex;align-items:flex-start}.header{padding:50px 0;display:flex;justify-content:center}.filter-transfer{box-shadow:0px 2px 8px rgba(0,0,0,0.1);background-color:#fff;border-radius:5px;padding-bottom:10px;margin-right:20px;min-width:232px}.filter-transfer__title{color:#4a4a4a;text-transform:uppercase;font-size:12px;width:100%;padding:20px;padding-bottom:10px}.filter-transfer__list{list-style:none}.filter-item{width:100%;font-family:'Open Sans', sans-serif;font-weight:400;font-size:13px;display:flex;align-items:center;color:#4a4a4a}.filter-item__label{width:100%;padding:10px 20px;padding-left:50px;position:relative}.filter-item__label:hover{cursor:url(data:image/png;base64,),pointer;background-color:#f1fcff}.filter-item__label::after{content:'';position:absolute;top:50%;left:20px;transform:translateY(-50%);width:20px;height:20px;border-radius:2px;border:1px solid #9abbce;display:flex;justify-content:center;align-items:center}.filter-item__input{display:none}.filter-item__input:checked+.filter-item__label::after{content:url(data:image/png;base64,);border:1px solid #2196f3}.filter-price{width:100%;display:flex}.filter-price__item{font-family:'Open Sans', sans-serif;font-weight:600;font-size:12px;padding:15px 0;flex-basis:50%;display:flex;justify-content:center;align-items:center;text-transform:uppercase;border:1px solid #dfe5ec;background-color:#fff;transition:all 0.3s ease-in-out}.filter-price__item:first-child{border-bottom-left-radius:5px;border-top-left-radius:5px}.filter-price__item:last-child{border-bottom-right-radius:5px;border-top-right-radius:5px}.filter-price__item:hover,.filter-price__item.active{cursor:url(data:image/png;base64,),pointer;border:1px solid #2196f3;background-color:#2196f3;color:#fff}.cart-ticket-item{width:100%;padding-top:10px;display:flex;justify-content:space-between}.cart-ticket-item-parts{font-family:'Open Sans', sans-serif;font-weight:600;width:calc(100% / 3)}.cart-ticket-item-parts .item-up{color:#a0b0b9;font-size:12px;text-transform:uppercase}.cart-ticket-item-parts .item-down{color:#4a4a4a;font-size:14px}.cart-ticket-item-parts:last-child{margin-right:0}.cart-ticket{width:100%;margin-bottom:20px;background-color:#fff;border-radius:5px;box-shadow:0px 2px 8px rgba(0,0,0,0.1);padding:20px}.cart-ticket:first-child{margin-top:20px}.cart-ticket__title{display:flex;justify-content:space-between;align-items:center;min-height:36px;padding-bottom:10px;padding-right:30px}.cart-ticket__title .price{font-family:'Open Sans', sans-serif;font-weight:600;font-size:24px;color:#2196f3;width:calc(100% * 2 / 3)}.cart-ticket__title .img{height:100%;display:flex;justify-content:flex-start;align-items:center;width:calc(100% / 3 - 20px)}.cart-ticket__title .img img{object-fit:cover}.main-carts{width:100%}.error-boundry{text-align:center;padding:10px}.error-boundry__title{margin-bottom:10px}.error-boundry .filter-price__item{margin:0 auto;border-radius:5px;padding:20px;width:50%}

